<template>
  <v-card class="mx-auto" style="max-width: 500px;margin-top:5%">

    <v-system-bar color="deep-purple darken-4" dark>
      <v-spacer></v-spacer>
      <v-icon small>mdi-square</v-icon>
      <v-icon class="ml-1" small>mdi-circle</v-icon>
      <v-icon class="ml-1" small>mdi-triangle</v-icon>
    </v-system-bar>

    <v-toolbar color="deep-purple accent-4" cards dark flat>
      <v-btn icon><v-icon>mdi-arrow-left</v-icon></v-btn>
      <v-card-title class="title font-weight-regular">Авторизация</v-card-title>
      <v-spacer></v-spacer>
      <v-btn icon><v-icon>mdi-magnify</v-icon></v-btn>
      <v-btn icon><v-icon>mdi-dots-vertical</v-icon></v-btn>
    </v-toolbar>

    <v-form ref="form" v-model="form" class="pa-4 pt-6">

     <v-text-field
        v-model="authData.login"
        filled
        color="deep-purple"
        label="login"
        type="text"
      ></v-text-field>

      <v-text-field
        v-model="authData.password"
        filled
        color="deep-purple"
        label="Password"
        style="min-height: 96px"
        type="password"
      ></v-text-field>

    </v-form>

    <v-divider></v-divider>

    <v-card-actions>

      <v-btn text @click="$refs.form.reset()">Clear</v-btn>

      <v-spacer></v-spacer>

      <v-btn style="border-radius:0px; width:30%"
        @click="auth()"
        :disabled="!form"
        :loading="isLoading"
        class="white--text"
        color="deep-purple accent-4"
      >Войти</v-btn>
    </v-card-actions>

    <v-dialog v-model="dialog" absolute max-width="400" persistent>
      <v-card>
        <v-card-title class="headline grey lighten-3">Legal</v-card-title>
        <v-card-text>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn text @click="agreement = false, dialog = false">No</v-btn>
          <v-spacer></v-spacer>
          <v-btn
            class="white--text"
            color="deep-purple accent-4"
            @click="agreement = true, dialog = false"
          >Yes</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-card>
</template>

<script>
export default {
  data: () => ({
    response: [],
    authData: {
      login: "in",
      password: "1234"
    },

    agreement: false,
    bio:"Far far away, behind the word mountains, far from the countries Vokalia and Consonantia, there live the blind texts",
    dialog: false,
    email: undefined,
    form: false,
    isLoading: false,
    password: undefined,
    phone: undefined,
    rules: {
      email: v => (v || "").match(/@/) || "Please enter a valid email",
      length: len => v =>
        (v || "").length >= len || `Invalid character length, required ${len}`,
      password: v =>
        (v || "").match(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*(_|[^\w])).+$/) ||
        "Password must contain an upper case letter, a numeric character, and a special character",
      required: v => !!v || "This field is required"
    }
  }),

  methods: {

    auth() {
      const apiUrl = 'post/auth/login';
      this.send(apiUrl, "post", this.authData).then(this.authResponse);
    },

    authResponse(response) {

         this.response = response;
         if(!this.response.status || !this.response.jwt_token) {
           alert('Не удалось авторизовать пользователя');
           return false;
         }

         const role = this.response.role;

         this.store('token'      , this.response['jwt_token']);
         this.store('user_id'    , this.response['user_id']);
         this.store('auth_status', this.response['status']);
         this.store('username'   , this.response['username']);
         this.store('role'       , role);

         this.redirectToPage(role);
    },

    redirectToPage(role) {
          switch (role) {

            case 1: 
               this.$router.push('/'); 
               break;

            case 2: 
               this.$router.push('/documents-page');
               break;

           case 3: 
               this.$router.push('/person-page');
               break;

           case 4: 
               this.$router.push('/company-page');
               break;   
         
           default: break;
         }
    },

  }
};
</script>

